import React, { useState } from 'react';
import Helmet from 'react-helmet';
import md5 from 'blueimp-md5';

import question1 from '../assets/images/question1.png';
import question2 from '../assets/images/question2.png';
import question3 from '../assets/images/question3.png';
import question4 from '../assets/images/question4.png';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';
import Lightbox from '../components/lightbox';

import { Link } from 'gatsby';
import { makeStyles } from '@material-ui/core/styles';
import { blue } from '@material-ui/core/colors';

const useStyles = makeStyles(() => ({
  hintlink: {
    color: blue[500],
    borderColor: blue[500]
  },
  strong: {
    fontWeight: "bold"
  }
}));

const Question = () => {
  const [answer, setAnswer] = useState('');
  const [submitDisabled, setSubmitDisabled] = useState(false);

  const submit = event => {
    event.preventDefault();
    setSubmitDisabled(true);

    const trimmedAnswer = answer.match(/^[\s　]*(.*?)[\s　]*$/)[1];
    const hashedAnswer = md5(trimmedAnswer);

    window.location.href = `../answer/${hashedAnswer}/`;
  };

  const classes = useStyles();

  return (
    <Layout>
      <section id="header">
        <div className="inner">
          <h1>問題はこちらです！</h1>
        </div>
      </section>
      <Helmet title="問題 | 氷蔵姉妹の挑戦" />

      <section id="one" className="main style1">
        <div className="grid-wrapper">
          <div className="col-12">
            <p>
              問題1, 2, 3をそれぞれ解いた後に最終問題を解き、<br/>
              その答えを下のテキストボックスに入力してください。
            </p>
          </div>
        </div>
        <div className="grid-wrapper separate">
          <div className="col-12">
            <Lightbox src={question1} />
          </div>
        </div>
        <div className="grid-wrapper separate">
          <div className="col-12">
            <Lightbox src={question2} />
          </div>
        </div>
        <div className="grid-wrapper separate">
          <div className="col-12">
            <Lightbox src={question3} />
          </div>
        </div>
        <div className="grid-wrapper separate">
          <div className="col-12">
            <Lightbox src={question4} />
          </div>
        </div>
        <div className="grid-wrapper">
          <div className="col-12">
            <p>
              <Link to="/hint/" className={classes.hintlink}>ヒント・解答はこちらから</Link>どうぞ。
            </p>
          </div>
        </div>
        <div className="grid-wrapper">
            <div className="col-12">
              <form onSubmit={submit}>
                <div>
                  <label>
                    解けたら、下のテキストボックスに最終問題の解答を<em className={classes.strong}>ひらがなで</em>入れてください。
                    <input
                      type="text"
                      value={answer}
                      onChange={event => setAnswer(event.target.value)}
                      required
                    />
                  </label>
                </div>
                <div>
                  <button type="submit" disabled={submitDisabled}>解答送信</button>
                </div>
              </form>
            </div>
          </div>
        <div className="grid-wrapper">
          <div className="col-12">
            <PageNavigation prev="/" />
          </div>
        </div>
      </section>
    </Layout>
  );
};

export default Question;
